@font-face {
    font-family: 'Gilroy';
    src: url('../../fonts/Gilroy-Bold.woff');
  }
  @font-face {
    font-family: 'Proxima Nova';
    src: url('../../fonts/proxima-nova-light-webfont.woff');
  }
//== Typography Variable
$baseFont: 'Proxima Nova';
$secondFont: 'Gilroy';
$baseFontSize: 16px;
$mdi-icon: 'Material Design Icons';
$fa-icon: 'FontAwesome';

//== Site Typography Colors
$paragraph: #1f212E;
$headingColor: #1f212E;
$ashColor: #aaaaaa;
$borderColor: #eee;
$hvrColor: #182141;
$bleu_ciel:  #2ea3dd;
$beige:  #e0ded2;

//== Site Brand Color
$brandColor: #1f212E;
$brandColorDark: darken($brandColor, 15%);
$brandColorLight: lighten($brandColor, 15%);

//== Alternative Color
$white: #FFFFFF;
$offWhite: #f8f9fc;
$black: #000000;
$blackSoft: #222222;
$navy: #3b5998;
$pink: #FF69B4;
$violet: #9933cc;
$olive: #559137;
$green: #008000;
$red: #e74c3c;
$orange: #f5740a;
$yellow: #fbbf00;
$softorange: #F4A259;
$tomatored: #F25C66;

//== Social Media Icons Color
$facebook: #3B5999;
$twitter: #1DA1F2;
$pinterest: #CB2028;
$youtube: #CC332D;
$reddit: #FF4500;
$linkedin: #0077B5;

//== Responsive Media Query Variables
$xlmax-device: 'only screen and (min-width: 1200px) and (max-width: 1599.98px)';
$xl-device: 'only screen and (min-width: 1200px)';
$lg-device: 'only screen and (min-width: 992px) and (max-width: 1199.98px)';
$md-device: 'only screen and (min-width: 768px) and (max-width: 991.98px)';
$sm-device: 'only screen and (max-width: 767.98px)';
$xs-device: 'only screen and (max-width: 575.98px)';
$xxs-device: 'only screen and (max-width: 479.98px)';