.profile-card{
  position:relative;
  overflow: hidden;
  height: 300px;
  width: 220px;
  box-shadow:0px 1px 1px #222;
  border-radius: 10%;
 
}
.profile-card:hover .profile-img img
{
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.profile-card .profile-img img{
  width:220px;
  height: 300px;
  
  transition: -webkit-transform 1s;
  
  transition: transform 1s;
  
  transition: transform 1s, -webkit-transform 1s;
}
.profile-card .profile-content::before{
  content: "";
  position:absolute;
  top:0;
  left:0;
  width:50%;
  height:40%;
  border:1px solid #333;
  border-right:3px solid transparent;
  border-bottom:3px solid transparent;
  -webkit-transform: scale(1,1);
          transform: scale(1,1);
  transition: all 0.50s linear;
}
.profile-card .profile-content::after{
  content: "";
  position:absolute;
  bottom:0;
  right:0;
  width:50%;
  height:40%;
  border:1px solid #222;
  border-left:3px solid transparent;
  border-top:3px solid transparent;
  transition: all 0.50s linear;
  
}
.profile-card:hover .profile-content:before{
  top:10px;
  left:10px;
}
.profile-card:hover .profile-content:after{
  bottom:10px;
  right:10px;
}

.profile-card .profile-content{
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  padding:50% 4%;
  text-align: center;
 
}
.profile-card .profile-content:hover{
  background-color: rgba(43, 142, 255, 0.5);
}
.profile-card .profile-content .title{
  font-size:24px;
  color:#333;
  opacity:0;
  letter-spacing:2px;
  text-transform:uppercase;
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
  transition:all 900ms cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.20s;
}
.profile-card:hover .title{
  opacity:1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.profile-card:hover .title::after{
  content:"";
  display:block;
  width:50%;
  margin:10px auto;
  border-bottom:2px solid #222;
}
.profile-card .profile-content .title > span{
  display:block;
  margin:5px 0;
  font-size: 14px;
  text-transform: capitalize;
}
.profile-card .profile-content .social-link{
  margin:20px 0;
  padding:0;
  opacity:0;
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
  transition:all 600ms cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.20s;
}
.profile-card:hover .social-link{
  opacity:1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.profile-card .profile-content .social-link li{
  display: inline-block;
  list-style: none;
  margin:0 4px;
}
.profile-card .profile-content .social-link li a{
  color:#333;
  width:30px;
  height:25px;
  line-height:24px;
  text-align: center;
  border-radius:5px;
  background-color: #fff;
}

